.navigationBar {
  padding: 0px 120px 0px 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media screen and (max-width: 768px) {
    padding: 0 15px;
  }
}

.navLinksContainer {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  @media screen and (max-width: 768px) {
    text-align: center;
  }
}

.navLink {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.44px;
  color: #5E6366;
  padding: 15px 20px;
  margin-right: 12px;
}

.navLink:hover {
  color: #000000;
}

.selected {
  color: rgba(0, 0, 0, 0.87);
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #C6CACC;
  border-bottom: 0;
  border-radius: 4px 4px 0px 0px;
}
